.row {
  margin: 0;
  padding: 0;

  width: 100%;
}

.square {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-left: 2px;
  margin-right: 2px;
  border: 1px solid black;

  padding: 0;
}

.fill {
  background-color: black;
  border-radius: 5px;
}
